.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: linear-gradient(#000000, #353535);
}

.title {
  font-size: 85px;
  color: white;
  animation: flipTitle 2s;
}

@keyframes flipTitle {
  from{
    transform: rotateX(90deg);
  }
  to{
    transform: rotateX(0deg);
  }
}

.containerInput {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 15px;
  margin: 34px 0;
  display: flex;
  border-radius: 8px;
  box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.5);
}

.containerInput input {
  background-color: transparent;
  border: 0;
  font-size: 20px;
  color: white;
  outline: none;
  margin-right: 8px;
}

.containerInput input::placeholder {
  color: #f1f1f1;
}

.buttonSearch {
  background-color: transparent;
  border: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
}

.buttonSearch:hover {
  transform: scale(1.4);
}

.main {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: white;
  width: 500px;
  border-radius: 8px;
}

.main h2 {
  margin: 16px 0;
  font-size: 39px;
}

.main span {
  margin-bottom: 16px;
  font-weight: bold;
}

.footer {
  color: white;
  border-top: 0.4px solid #ffffff;
  bottom: 0;
  width: 100%;
  position: absolute;
  height: 49px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footerlink {
  text-decoration: none;
  color: inherit;
  transition: 0.5s;
}

.footerlink:hover {color: #05adfb;}

@media (max-width: 620px) {
  .title{
    font-size: 60px;
    text-align: center;
  }
  .main {
    width: 80%;
    min-height: 250px;
  }
  .main h2 {
    font-size: 28px;
  }
}